.header {
  @include width-block(auto);
  min-height: 617px;
  padding-top: 27px;

  text-align: center;
  line-height: 1.4;
  background-color: $bg-secondary;
  background-image: url("../img/cat-promo.png");
  background-repeat: no-repeat;
  background-position: 100% 100%;
}

.header__container {
  @include width-block(320px);
  padding: 0 20px;
}

.header__logo {
  display: block;
  margin-bottom: 48px;
}

.header__social {
  display: none;
}

.header__city {
  margin: 0;
  color: $color-secondary;
  font-size: 14px;
}

.header__title {
  margin-top: 5px;
  margin-bottom: 15px;
  font-weight: normal;

  font-family: $font-secondary;
  font-size: 36px;
  line-height: 1.2;
}

.header__lead {
  margin-bottom: 35px;
font-size: 14px;
line-height: 1.4;


color: $color-secondary;

}


@media (min-width: $tablet-width) {
  .header {
    min-height: 595px;
    padding-top: 28.5px;

    text-align: left;

    background-image: url("../img/cat-promo@tablet.png");
    background-position: 100% 100%;
  }

  .header__container {
    @include width-block(768px);
    padding: 0 43px;
  }

  .header__wrapper {
    width: 400px;
  }

  .header__logo {
    text-align: center;
  }

  .header__city {
    font-size: 16px;
  }

  .header__title {
    font-size: 52px;
  }

  .header__lead {
    width: 250px;

    font-size: 16px;
  }

  .header__btn {
    padding: 18.34px 35px 17.33px;

    font-size: 14px;
  }

  .header__social {
    display: flex;
    flex-wrap: wrap;
    margin-top: 60px;
  }

  .header__social {
    display: flex;
    flex-wrap: wrap;
    margin-top: 60px;
  }

  .header__social-item {
    &:not(:first-child) {
      margin-left: 11px;
    }

    &:hover,
    &:focus {
      outline: none;
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }

  .header__social-item svg {
    fill: #ffffff;
  }
}

@media (min-width: $desktop-width) {
  .header {
    min-height: 710px;
    padding-top: 28.5px;

    background-image: url("../img/cat-promo@decstop.png");
    background-position: 100% 100%;
  }

  .header__container {
    @include width-block(1200px);
    padding: 0 40px 0 38px;
  }

  .header__wrapper {
    width: auto;
  }

  .header__content {
    width: 400px;
    padding-left: 150px;
  }

  .header__logo {
    margin-bottom: 96px;
  }

  .header__lead {
    width: 300px;
  }

  .header__social {
    flex-direction: column;
    margin-top: 60px;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: 0px;
      height: 77.5px;
      left: 8px;
      top: -102px;
      border: 0.5px solid rgba(255, 255, 255, 0.5);
    }
  }

  .header__social-item {
    &:not(:first-child) {
      margin-left: 0px;
      margin-top: 15px;
    }
  }
}
